html {
  background-color: #fdfdfd;
}
.app {
  margin: 0;
  font-family: "Poppins", sans-serif;
  letter-spacing: 1px;
  line-height: 2em;
}
.line {
  display: block;
  width: 30px;
  height: 1px;
  line-height: 0;
  font-size: 0;
  border-bottom: 1px solid #000;
  margin: 0 auto;
}
/*-------------------header-------------------*/
.header {
  background: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  margin: 1em 20em;
}
@media (max-width: 1100px) {
  .header {
    margin: 2em;
  }
}
@media (max-width: 500px) {
  .header {
    margin: 1em;
  }
}

.header-title {
  text-decoration: none;
  font-size: 1.25em;
  color: inherit;
  text-transform: uppercase;
}
@media (max-width: 500px) {
  .header-title {
    font-size: 1.25em;
    color: inherit;
    text-transform: uppercase;
    margin: 1em;
  }
}
@media (max-width: 500px) {
  .main-nav {
    visibility: hidden;
  }
}
.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-link {
  --spacing: 1em;
  text-decoration: none;
  display: inline-block;
  padding: calc(var(--spacing) / 2) var(--spacing);
  position: relative;
  letter-spacing: 1px;
  font-size: 1.15em;
  color: inherit;
}

.nav-link:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: var(--spacing);
  right: var(--spacing);
  height: 1px;
  background: currentColor;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  transition: -webkit-transform 150ms ease-in-out;
  transition: transform 150ms ease-in-out;
  transition: transform 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
}

.nav-link:hover::after {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
/*-------------------home-------------------*/
.home {
  margin: 6em auto;
  width: 1000px;
  font-size: 1em;
}
.home-content {
  font-size: 1.25em;
  width: 460px;
  margin: 5em 2em 3em 2em;
  text-align: left;
}
.each-project {
  margin: 0 2em 4.15em 2em;
}
.each-project-img {
  width: 700px;
  object-fit: cover;
}
.each-project-name {
  margin: 0;
  text-align: left;
  font-size: 1.15em;
  color: black;
  /* text-decoration: none; */
}
.each-project-tech {
  font-size: 1em;
  margin: 0;
  text-align: left;
}
.home-footer{
  margin: 0 2em;
}
@media (max-width: 1100px) {
  .home {
    margin: 10em auto;
    width: 700px;
  }
}
@media (max-width: 500px) {
  .home {
    margin: 10em auto;
    width: 300px;
  }
  .each-project-img {
    width: 300px;
    object-fit: cover;
  }
  .home-content{
    width: 250px;
    font-size: 1.15em;
    text-align: left;
    margin: 1.5em;
  }
}

/*-------------------footer-------------------*/
.footer {
  font-size: 0.9em;
}
.footer-link {
